.cartdrawer {
  background-color: rgb(0, 162, 255);
  height: 40px;
  overflow: hidden;
  opacity: 0;
  border-bottom: 2px solid black;
  visibility: hidden;
  transition: visibility 0.3s, opacity 0.3s linear, height 0.3s ease-in-out;
}

.cartdrawer > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.outerbox {
  margin: 0 auto;
  margin-top: 5px;
  margin-bottom: 5px;
  display: flex;
}

.box {
  display: flex;
  background-color: rgb(255, 255, 255);
  padding: 5px 15px;
  text-align: center;
  border: solid grey 0.5px;
  justify-content: center;
  /* height: 20px; */
}

.box > div {
  border-right: solid grey 1px;
}

.expanded {
  height: 7.5rem;
}

.visible {
  opacity: 1;
  visibility: visible;
}

.toggle {
  position: absolute;
  right: 30%;
}

.noflex {
  display: block !important;
  padding: 0 20%;
}
