p {
  margin: 0;
  margin-right: 30px;
}

.ticker svg,
.clearButton svg {
  pointer-events: none;
}

#clear {
}

.clearButton svg {
  color: rgb(136, 67, 67);
  pointer-events: none;
}

.subtype {
  display: flex;
  align-items: center;
  width: 80%;
  font-size: 4vw;
  text-align: center;
  border: 1px solid black;
  border-radius: 5px;
  background-color: white;
  margin: 0 auto;
  padding: 10px 40px;
  margin-bottom: 10px;
  transition: all 0.3s ease-in-out;
  box-shadow: 2px 2px 2px rgb(129, 125, 125);

  @media (min-width: 700px) {
    font-size: 23px;
    max-width: 450px;
  }
}

.subtype:hover {
  background-color: blanchedalmond;
}

.subtype:hover span {
  color: rgb(97, 155, 42);
}

.subPrice {
  transition: all 0.3s ease-in-out;
}

.counter {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 20px 0 0;
}

.typetotal {
  width: 30px;
  height: 2rem;
  border: 2px solid rgb(129, 127, 127);
  border-radius: 20%;
  margin: 0 15px 0 15px;
  text-align: center;
}

input[type="number"] {
  -webkit-appearance: textfield;
  -moz-appearance: textfield;
  appearance: textfield;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

.ticker {
  cursor: pointer;
  font-size: 1rem;
  line-height: 1;
  width: max-content;
  text-align: center;
}

.clearButton {
  font-size: 1rem;
}
